
import React, { useRef, useState } from "react";

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

import image1 from "./images/00.webp"
import image2 from "./images/abeer.webp"
import image3 from "./images/farhan.webp"
import image4 from "./images/tausif.webp"
import image5 from "./images/zarif.webp"

// Import Swiper styles
import "swiper/swiper.min.css";
import "swiper/components/effect-coverflow/effect-coverflow.min.css"
import "swiper/components/effect-fade/effect-fade.min.css"

import "./styles.css";


// import Swiper core and required modules
import SwiperCore, { EffectCoverflow, EffectFade, Controller } from 'swiper/core';

// install Swiper modules
SwiperCore.use([EffectCoverflow, EffectFade, Controller]);

const images = [image1, image2, image3, image4, image5]

export default function App() {

  const [controlledSwiper, setControlledSwiper] = useState(null)

  return (
    <>
      <Swiper id="controller" onSwiper={setControlledSwiper} spaceBetween={30} effect={'fade'} className="golapi-swiper-fade" allowTouchMove={false}>
        <SwiperSlide><img src={images[0]} /></SwiperSlide>
        <SwiperSlide><img src={images[1]} /></SwiperSlide>
        <SwiperSlide><img src={images[2]} /></SwiperSlide>
        <SwiperSlide><img src={images[3]} /></SwiperSlide>
        <SwiperSlide><img src={images[4]} /></SwiperSlide>
      </Swiper>
      <Swiper
        className="golapi-swiper-coverflow"
        controller={{control: controlledSwiper}}
        effect={'coverflow'}
        grabCursor={true}
        centeredSlides={true}
        slidesPerView={'auto'}
        coverflowEffect={{
          "rotate": 0,
          "stretch": 10,
          "depth": 1000,
          "modifier": 1,
          "slideShadows": true
        }}>
        <SwiperSlide><img src={images[0]} /></SwiperSlide>
        <SwiperSlide><img src={images[1]} /></SwiperSlide>
        <SwiperSlide><img src={images[2]} /></SwiperSlide>
        <SwiperSlide><img src={images[3]} /></SwiperSlide>
        <SwiperSlide><img src={images[4]} /></SwiperSlide>
      </Swiper>
    </>
  )
}